import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/home',
        name: 'home',
        component: HomeView

    },
    {
        path: '/el',
        name: 'el',
        component: () => import('@/views/Element.vue')
    },{
        path: '/jobDetails',
        name: 'jobDetails',
        component: () => import('@/views/jobDetails.vue')
    }, {
        path: '/companyUpdatePost',
        name: 'companyUpdatePost',
        component: () => import('@/views/companyUpdatePost.vue')
    },{
        path: '/componentPosts',
        name: 'componentPosts',
        component: () => import('@/views/componentPosts.vue')
    },
    {
        path: '/companyPost',
        name: 'companyPost',
        component: () => import('@/views/companyPost.vue')
    },
    {
        path: '/resume',
        name: 'resume',
        component: () => import('@/views/resume.vue')
    }, {
        path: '/companyInformation',
        name: 'companyInformation',
        component: () => import('@/views/companyInformation.vue')
    },
    {
        path: '/componentRegister',
        name: 'componentRegister',
        component: () => import('@/views/componentRegister.vue')
    },
    {
        path: '/viewUser',
        name: 'viewUser',
        component: () => import('@/views/viewUser.vue')
    },
    {
        path: '/componentHomeView',
        name: 'componentHomeView',
        component: () => import('@/views/componentHomeView.vue')
    },
    {
        path: '/componentInterview',
        name: 'componentInterview',
        component: () => import('@/views/componentInterview.vue')
    },
    {
        path: '/componentElement',
        name: 'componentElement',
        component: () => import('@/views/componentElement.vue')
    },
    {

        path: '/componentLogin',

        name: 'componentLogin',

        component: () => import('@/views/componentLogin.vue')
    },
    {

        path: '/interview',

        name: 'interview',

        component: () => import('@/views/interview.vue')
    },
    {

        path: '/recruit',

        name: 'recruit',

        component: () => import('@/views/recruit.vue')
    },
    {

        path: '/self',

        name: 'self',

        component: () => import('@/views/self.vue')
    },
    {

        path: '/',

        name: 'kong',

        redirect: '/login'
    },
    {

        path: '/register',

        name: 'register',

        component: () => import('@/views/register.vue')
    },
    {

        path: '/login',
        name: 'login',
        component: () => import('@/views/login.vue')
    }
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router
